// 变化 创建一个对象存储一系列我们接下来要写的 mutation 函数
export const mutations = {
	// TODO: 放置我们的状态变更函数
	// mutation 的第一个参数是当前的 state你可以在函数里修改 state
	changeUserName (state,curName) {
		state.username = curName;
		// console.log(state.username+"state.username");
	}
};


