<template>
<div id="m-blog">
  <div class="blogNav">
    <ul>
      <li @click="changeIsR('css')">css</li>
      <li @click="changeIsR('js')">js</li>
      <li @click="changeIsR('vue')">vue</li>
      <li @click="changeIsR('react')">react</li>
      <li @click="changeIsR('angular')">angular</li>
      <li @click="changeIsR('php')">php</li>
      <li @click="changeIsR('node')">node</li>
    </ul>
  </div>
  <div class="blogContent">
    <BlogCss v-if="isRouter=='css'"></BlogCss>
    <blog-js v-if="isRouter=='js'"></blog-js>
    <blog-vue v-if="isRouter=='vue'"></blog-vue>
    <blog-react v-if="isRouter=='react'"></blog-react>
    <blog-angular v-if="isRouter=='angular'"></blog-angular>
    <blog-php v-if="isRouter=='php'"></blog-php>
    <blog-node v-if="isRouter=='node'"></blog-node>
  </div>
    
</div>
</template>


<script>
  import BlogCss from './blog-css'
  import BlogJs from './blog-js'
  import BlogVue from './blog-vue'
  import BlogReact from './blog-react'
  import BlogAngular from './blog-angular'
  import BlogPhp from './blog-php'
  import BlogNode from './blog-node'
  
  export default {
    name:'blog',
    components: {
      BlogCss,
      BlogJs,
      BlogVue,
      BlogReact,
      BlogAngular,
      BlogPhp,
      BlogNode
    },
    data () {
      return {
        isRouter:'css'
      }
      
    },
    methods:{
      changeIsR: function (str) {
        this.isRouter = str
        // console.log(this.isRouter)
      }
    },
    mounted () {
     
    }
  }
</script>



