<template>
<div id="m-b-css">
  <aside class="b-css-aside">
    <ul>
      <li v-on:click="getId('js'+i)" v-for="(item,i) in estitle"> {{ item }}</li>
    </ul>
  </aside>
  <div class="b-css-main">
    <BlogJsHandwrite0 v-bind:essayData="essay" v-bind:dataId="getid" v-bind:commeRes="commeRes" v-if="getid == 'js0'"></BlogJsHandwrite0>
    <BlogJsHandwrite1 v-bind:essayData="essay" v-bind:dataId="getid" v-bind:commeRes="commeRes" v-if="getid == 'js1'"></BlogJsHandwrite1>
     <BlogJsHandwrite2 v-bind:essayData="essay" v-bind:dataId="getid" v-bind:commeRes="commeRes" v-if="getid == 'js2'"></BlogJsHandwrite2>
  </div>
</div>
</template>


<script>
  // 1.getId('css'+i)   2.estitle:[]   3.essay数据库 4.组件引入
  import BlogJsHandwrite0 from './blog-js-handwrite0'
  import BlogJsHandwrite1 from './blog-js-handwrite1'
  import BlogJsHandwrite2 from './blog-js-handwrite2'  
  import common from '@/utils/util.common.js' 

  export default {
    name:'blog-js',
    components:{
      BlogJsHandwrite0,
      BlogJsHandwrite1,
      BlogJsHandwrite2
    },
    data () {
      return {
        essay:[],
        estitle:['ES5 数组的扩展方法','排序算法','常见兼容问题总结'],
        getid:'',
        commeRes:[]
      }
      
    },
    methods: {
        getId: function(id){
            this.getid = id
            var that = this
            // console.log(that.getid)
            if(that.getid != ''){
                // console.log('执行')
                // common.ajax("POST",'/api/essay','id='+that.getid,function (res) {
                //     that.essay = res
                // })
            }
            common.ajax("POST",'/api/list','dataId='+that.getid+'comme',function (res) {
              // console.log(res)
              that.commeRes = res
              // console.log(that.commeRes)
              // console.log(that.commeRes[0].uid+'uid')
            })
        }
    },
    mounted () {
        
    }
  }
</script>

<style scoped>
    #m-b-css{
        width: 100%;
    }
    .b-css-aside{
        width: 100%;
    }
    .b-css-aside ul{
        padding:0 0.15rem;
    }
    .b-css-aside ul li{
        font-size: .2rem;
        border-top: #ccc solid 1px;
        border-bottom: #ccc solid 1px;
    }
    .b-css-main{

    }
</style>


