<template>
<div id="m-b-css">
  <aside class="b-css-aside">
    <ul>
      <li v-on:click="getId('css'+i)" v-for="(item,i) in estitle"> {{ item }}</li>
    </ul>
  </aside>
  <div class="b-css-main">
    <blog-template v-bind:essayData="essay" v-bind:dataId="getid" v-bind:commeRes="commeRes"></blog-template>
  </div>
</div>
</template>


<script>
  // 1.getId('css'+i)   2.estitle:[]   3.essay数据库
  import BlogTemplate from './blog-template'
  import common from '@/utils/util.common.js' 

  export default {
    name:'blog-css',
    components:{
      BlogTemplate
    },
    data () {
      return {
        essay:[],
        estitle:['Less基础','Sass基础','弹性盒模型'],
        getid:'',
        commeRes:[]
      }
      
    },
    methods: {
        getId: function(id){
            this.getid = id
            var that = this
            // console.log(that.getid)
            if(that.getid != ''){
                // console.log('执行')
                common.ajax("POST",'/api/essay','id='+that.getid,function (res) {
                    that.essay = res
                })
            }
            common.ajax("POST",'/api/list','dataId='+that.getid+'comme',function (res) {
              // console.log(res)
              that.commeRes = res
              // console.log(that.commeRes)
              // console.log(that.commeRes[0].uid+'uid')
            })
        }
    },
    mounted () {
        
    }
  }
</script>

<style scoped>
    #m-b-css{
        width: 100%;
    }
    .b-css-aside{
        width: 100%;
    }
    .b-css-aside ul{
        padding:0 0.15rem;
    }
    .b-css-aside ul li{
        font-size: .2rem;
        border-top: #ccc solid 1px;
        border-bottom: #ccc solid 1px;
    }
    .b-css-main{

    }
</style>


