var common = {
  render: function (str) {
    var body = document.body;
    body.innerHTML = str + body.innerHTML;
  },

  isAllLoaded(scope, cb) {
      var t_img; // 定时器
      var isLoad = true; // 控制变量

      // 判断图片加载状况，加载完成后回调
      return isImgLoad(cb);

      // 判断图片加载的函数
      function isImgLoad(callback) {
          // 查找所有图片，迭代处理
          //console.log(scope)
          //console.log(typeof scope)
          var sco = document.getElementById(scope);
          //console.log(sco)
          var tar = sco.getElementsByTagName("img");
          //console.log(tar)
          //问题
          // for(var key in tar){
          //   //for in 会将length 和其它属性打印出来
          //   console.log(tar[key])
          // }
          //解决方法一
          // function realArray(c) {
          //   try {
          //     return Array.prototype.slice.call(c);
          //   } catch (e) {
          //     var ret = [], i = 0, len = c.length;
          //     for (; i < len; i++) {
          //       ret[i] = (c[i]);
          //     }
          //     return ret;
          //   }
          // }
          // for(var key in realArray(tar)){
          //   //for in 会将length 和其它属性打印出来但是伪数组经过处理后就没问题了
          //   console.log(realArray(tar)[key])
          // }

          var getStyle = (function(){
             if(window.VBArray){
               return function(ele, attr){
                 return ele.currentStyle[attr];
               }
             }else{
               return function(ele, attr){
                 return getComputedStyle(ele,null)[attr];
               }
             }
          })();

          //解决方法二
          for(var i=0; i<tar.length; i++){
            //console.log(tar[i])
            // 找到为0就将isLoad设为false，并退出each
            if ( getStyle(tar[i], "height") === 0) {
                isLoad = false;
                return false;
            }
          }

          // 为true，没有发现为0的。加载完毕
          if (isLoad) {
              clearTimeout(t_img); // 清除定时器
              // 回调函数
              callback();
          // 为false，因为找到了没有加载完成的图，将调用定时器递归
          } else {
              isLoad = true;
              t_img = setTimeout(function() {
                  isImgLoad(callback); // 递归扫描
              }, 500); // 我这里设置的是500毫秒就扫描一次，可以自己调整
          }
      }
  },

  scroll: function (that,n) {
    var myScroll = new IScroll('#iscrollWrapper', {
        probeType: 3,//probeType：3  每滚动一像素触发一次
        mouseWheel: true
    });

    var scrollHeight = 35;
    //首先将页面上的上拉文字隐藏，等图片显示出来再讲上拉文字显示
    //$('.head.hide').removeClass('hide');
    //$('.foot.hide').removeClass('hide');
    that.isTrue = false;
    // 相对于当前位置滚动scrollBy（x，y，time，easing）
    myScroll.scrollBy(0, -scrollHeight);

    //改变箭头的上下方向
    //选取img 并判断有没有类名
    //  var head = $('.head img'),
    //      topImgHasClass = head.hasClass('up');
    //  var foot = $('.foot img'),
    //      bottomImgHasClass = head.hasClass('down');
    //滑动中
    myScroll.on('scroll', function () {
        var y = this.y,//当前位置
            //maxScrollY:当滚动到底部时的 myScroll.x/y    -2 - -1 = -1; -1*(2-1) = -1;;;;   maxY:剩余滚到底部的距离
            maxY = this.maxScrollY - y;
        // if (y >= 0) {
        //     !topImgHasClass && head.addClass('up');
        //     return '';
        // }
        // if (maxY >= 0) {
        //     !bottomImgHasClass && foot.addClass('down');
        //     return '';
        // }

        //当超出顶部时
        if (y >= 0) {
            that.headFalse = true;
            return '';
        }
        //当超出底部时
        if (maxY >= 0) {
            that.footFalse = true;
            return '';
        }
    });
    //滑动结束
    var n=0;
    myScroll.on('scrollEnd', function () {
        //如果在scrollHeight之上的滚到scrollHeight
        if (this.y >= -scrollHeight && this.y < 0) {
            myScroll.scrollTo(0, -scrollHeight);
            /*myScroll.scrollTo(0, -100);
            这将向下滚动100像素。记住：0总是左上角。要滚动你必须传递负数。
            time并且easing是可选的。他们分别调节动画的持续时间（以ms为单位）和缓动函数。
            缓动函数在IScroll.utils.ease对象中可用。例如，应用1秒弹性缓动你会做：
            myScroll.scrollTo(0, -100, 1000, IScroll.utils.ease.elastic);
            可用的选项有：quadratic，circular，back，bounce，elastic。*/

            //head.removeClass('up');
            that.headFalse = false;
        } else if (this.y >= 0) {
            //head.attr('src', '/images/ajax-loader.gif');
            that.src = '/static/images/ajax-loader.gif';
            //TODO ajax下拉刷新数据
            setTimeout(function () {
                myScroll.scrollTo(0, -scrollHeight);
                //head.removeClass('up');
                that.headFalse = false;
                //head.attr('src', '/images/arrow.png');
                that.src = '/static/images/arrow.png'
            }, 100);
        }
        //maxY:剩余滚到底部的距离
        var maxY = this.maxScrollY - this.y;
        if (maxY > -scrollHeight && maxY < 0) {
            var self = this;
            //滚到离底部scrollHeight的距离处
            myScroll.scrollTo(0, self.maxScrollY + scrollHeight);
            //foot.removeClass('down')
            that.footFalse = false;

        } else if (maxY >= 0) {
            //foot.attr('src', '/images/ajax-loader.gif');
            that.src = '/static/images/ajax-loader.gif';
            //TODO ajax上拉加载数据
            var self = this;

            // fetch('/api/iscroll')
            // .then(response => response.json())
            // .then(res => {
            //   that.isc = that.isc.concat(res);

            //   myScroll.refresh();

            //   //myScroll.scrollTo(0, self.y + scrollHeight);
            //   myScroll.scrollTo(0, self.maxScrollY - 60);

            //   //foot.removeClass('down');
            //   that.footFalse = false;
            //   //foot.attr('src', '/images/arrow.png');
            //   that.src = '/static/images/arrow.png'
            // })

            n++;
            //console.log(n)
            common.ajax("POST",'/api/iscroll','len=2&n='+n,function (res) {
              if (res) {
                that.isc = that.isc.concat(res);
              }
              
              myScroll.refresh();

              //myScroll.scrollTo(0, self.y + scrollHeight);
              myScroll.scrollTo(0, self.maxScrollY - 60);

              //foot.removeClass('down');
              that.footFalse = false;
              //foot.attr('src', '/images/arrow.png');
              that.src = '/static/images/arrow.png'
              //console.log(res)
              if (res == false) {
                //console.log(true)
                that.isTrue = true;
              }
            })


        }
    })
  },//scroll end
  packageSwiper(name){
		return new Swiper(name, {
			autoplay: 5000,//可选选项，自动滑动
            initialSlide :0,//初始索引
            direction : 'horizontal',//滑动方向水平|垂直vertical
            speed:300,//滑动速度，滑动开始到结束的时间
            autoplayDisableOnInteraction : false,//手滑后自动播放不停止
            autoplayStopOnLast : false,//如果设置为true，当切换到最后一个slide时停止自动切换。（loop模式下无效）
            grabCursor : true,//设置为true时，鼠标覆盖Swiper时指针会变成手掌形状，拖动时指针会变成抓手形状
            parallax : true,//视差效果，随着banner滑动，而位移
            hashnav:false,//当你的swiper切换时你的页面url就会加上这个属性的值了，你也可以通过进入页面时修改页面url让swiper在初始化时切换到指定的slide。
            hashnavWatchState:false,
            setWrapperSize :true,//开启这个设定会在Wrapper上添加等于slides相加的宽高，在对flexbox布局的支持不是很好的浏览器中可能需要用到
            virtualTranslate : false,//true：Swiper不会移动
            roundLengths : true,//true将slide的宽和高取整(四舍五入)以防止某些分辨率的屏幕上文字或边界(border)模糊。
            autoHeight: true,//true时，wrapper和container会随着当前slide的高度而发生变化
            a11y: true,//无障碍阅读
            prevSlideMessage: 'Previous slide',
            nextSlideMessage: 'Next slide',
            firstSlideMessage: 'This is the first slide',
            lastSlideMessage: 'This is the last slide',
            paginationBulletMessage:'Go to slide {{index}}',
            nextButton: '.swiper-button-next',//前进按钮
            prevButton: '.swiper-button-prev',//后退按钮
            pagination : '.swiper-pagination',//分页
            //pagination : '#swiper-pagination1',
            paginationHide :false,//true隐藏分页器
            paginationType : 'bullets',//分页器样式类型:‘bullets’  圆点（默认）‘fraction’  分式 ‘progress’  进度条‘custom’ 自定义
            paginationClickable :true,//true时，点击分页器的指示点分页器会控制Swiper切换
            //paginationElement : 'li',//设定分页器指示器（小点）的HTML标签。
        /* paginationBulletRender: function (swiper, index, className) {
    return '<span class="' + className + '">' + (index + 1) + '</span>';
    },*///这个参数允许完全自定义分页器的指示点。接受指示点索引和指示点类名作为参数。fraction/progress/custom也都有一个，详情见API
                //uniqueNavElements :false,//默认为true时，仅本swiper的container内的分页器有效，设置为false后，container以外的分页器也生效了。
                //scrollbar:'.swiper-scrollbar',//滚动条
                //scrollbarHide:false,//true：隐藏滚动条
                //scrollbarDraggable : true ,//为true时允许拖动滚动条。
                //scrollbarSnapOnRelease : true ,//释放滚动条自动贴合，不会半屏卡主

            effect: 'cube',//slide的切换效果，默认为"slide"（位移切换），可设置为"fade"（淡入）"cube"（方块）"coverflow"（3d流）"flip"（3d翻转）
            cube: {
            slideShadows: true,//slideShadows：开启slide阴影。默认 true。
            shadow: true,//shadow： 开启投影。默认 true。
            shadowOffset: 100,//shadowOffset：投影距离。默认 20，单位px。
            shadowScale: 0.6//shadowScale： 投影缩放比例。默认0.94。
            },
            //mousewheelControl : true,//鼠标滚轮控制
            lazyLoading : true,//true开启图片延迟加载
            lazyLoadingInPrevNext : true,//将延迟加载应用到最接近的slide的图片（前一个和后一个slide）。
            lazyLoadingInPrevNextAmount : 2,//默认为1，提前1个slide加载图片，例如切换到第三个slide时加载第四个slide里面的图片。
            loop : true,//循环
            freeMode : false
		})//Swiper end
		
    },//packageSwiper end
    ajax(type,url,data,callback){
  	  //创建一个ajax对象
  	  var xhr;
  		// console.log(xhr);
      var getxhr = (function () {
        if(window.XMLHttpRequest){
          return function () {
            xhr = new XMLHttpRequest();
          }
    		}else{
          return function () {
            xhr = new ActiveXObject("Microsoft.XMLHTTP");
          }
    		}
      })();
  		getxhr();
  		//连接服务器
  		xhr.open(type,url,true);
      //设置头信息
      if (type == 'POST') {
        xhr.setRequestHeader("Content-Type","application/x-www-form-urlencoded;charset=UTF-8");
      }
  		//处理请求
  		xhr.onreadystatechange=function(){
        if(xhr.readyState==4&&xhr.status==200){
          var obj = JSON.parse(xhr.responseText);
          //console.log(obj)
          callback(obj);
        }else{
          // console.log(xhr.readyState +" "+xhr.status);
        }
  		}
  		//发送参数
      if (type == 'POST') {
        var da = data;
      } else {
        var da = null;
      }
  		xhr.send(da);
  },//ajax end
  getCookie(cname){
    var name = cname + "=";
    var ca = document.cookie.split(';');
    for(var i=0; i<ca.length; i++) {
      var c = ca[i].trim();
      if (c.indexOf(name)==0) return c.substring(name.length,c.length);
    }
    return "";
  },// getcookie end
  // 获取form表单字符串，参数form表单对象
  formToString (formObj) {
    var allStr = ''
    if (formObj) {
      var elementsObj = formObj.elements
      var obj
      if(elementsObj){
        for(var i=0; i<elementsObj.length; i++){
          obj=elementsObj[i]
          if(obj.name != undefined && obj.name != ""){
            allStr+="&"+obj.name+"="+encodeURIComponent(obj.value)
          }
        }
      }else{
        alert("没有elements对象!")
        return
      }
    }else{
      alert("form不存在!")
      return
    }
    return allStr
  }//getform end
}//common end

module.exports = common;



