<template>
  <div id="app1">
    <!--<img src="./assets/logo.png">-->
    
    <header>
      <h1><a href="#" title="weiya" class="header_a">weiya溦漄代码
      </a></h1>
      <a href="#">欢迎来到溦漄代码~~</a>
      <ul>
        <li v-if="!usn"><router-link v-bind:to="'/login'">登录</router-link></li>
        <li v-else>{{ usn }}~<a v-on:click="logOu()">注销</a></li>        
        <li><router-link v-bind:to="'/register'">注册</router-link></li>
      </ul>
    </header>

    <nav id="navpc">
      <div v-bind:class="'navpcpre'+navIndex"></div>
      <ul>
        <li v-for="(item,i) in pcNav" v-bind:class="{ navpcfirsta:i == navIndex }" v-on:click="navpcfirstClick(i)"><router-link :to="item.router">{{ item.title }}</router-link></li>
      </ul>
    </nav>
    <router-view></router-view>
    <footer>
      <router-link :to="'/'">
        <div id="index" class="yo-ico">&#xe610;<span>主页</span></div>
      </router-link>
      <router-link :to="'/demo'">
        <div id="demo" class="yo-ico">&#xe601;<span>案例</span></div>
      </router-link>
      <router-link :to="'/blog'">
        <div id="blog" class="yo-ico">&#xe60d;<span>博客</span></div>
      </router-link>
      <router-link :to="'/scrawl'">
        <div id="scrawl" class="yo-ico">&#xe63d;<span>涂鸦</span></div>
      </router-link>
    </footer>
    
    <div id="bottom">
      <a target="_blank" href="http://www.miitbeian.gov.cn/" style="display: block;">冀ICP备16026330号-1</a>
      
      <a target="_blank" href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=13108202000383" style="display:block;text-decoration:none;height:20px;line-height:20px;"><img src="/static/images/beian.png" style="float:left;"/><p style="float:left;height:20px;line-height:20px;margin: 0px 0px 0px 5px; color:#939393;">冀公网安备 13108202000383号</p></a>
      <a href="javascript:;">
        邮箱：857091291@qq.com
      </a>
    </div>
  </div>
</template>

<script>
import common from '@/utils/util.common.js'
export default {
  name: 'app',
  data: function () {
    return {
      navIndex: 0,
      pcNav: [{
        router: '/',
        title: '首页'
      }, {
        router: '/demo',
        title: '案例'
      }, {
        router: '/blog',
        title: '博客'
      }, {
        router: '/scrawl',
        title: '涂鸦'
      }]
    }
  },
  computed: {
    usn () {
      return this.$store.getters.getUserName
    }
  },
  methods: {
    logOu: function () {
      var that = this
      common.ajax("POST",'/api/logout','',function (res) {
      })
      that.$router.push('/')
      // console.log("ajxlogout")
      that.$store.dispatch('commitUserName',null)      
    },
    navpcfirstClick: function (i) {
      this.navIndex = i
    }
  },
  beforeCreate () {
    // console.log('App.vue.beforeCreate')
	},
  created () {
    // 读取cookie设置状态
    var that = this
    // console.log(that.usn+"aaaaaaaaaaaaaaaaa")
    common.ajax("POST",'/api/testsession','',function (res) {
      if (res) {
        // console.log(res+'testsession')
        if (!that.usn) {
          var cook = common.getCookie("usernam")
          that.$store.dispatch('commitUserName',cook)
          // console.log(that.usn+"bbbbbbbbbbbbbbbbbbbb")
        }
      }
    })
  },
  updated () {
    // console.log('App.vue.updated')
  }
}
</script>




