<template>
    <div id="m-home">
        <div id="mainWrapper">
            <div id="banner">
                <div class="swiper-container swiper_c_modi">
                    <!-- 视差 -->
                    <div data-swiper-parallax="-15%" data-swiper-parallax-duration="3000" style="background:url(/static/images/banbg.png)" class="parallax"></div>
                    <!-- 内容 -->
                    <div class="swiper-wrapper">
                        <div v-for="item in swip" class="swiper-slide swiper-lazy" data-swiper-parallax="-100">
                            <img :src="item" />
                            <!-- 延迟加载 -->
                            <div class="swiper-lazy-preloader"></div>
                        </div>                     
                    </div>
                    <!--内容end-->
                    <!-- 分页 -->
                    <div class="swiper-pagination"></div>
                    <!-- 前进后退按钮 -->
                    <div class="swiper-button-prev swiper-button-white"></div>
                    <div class="swiper-button-next swiper-button-white"></div>
                    <!-- 滚动条 -->
                    <!-- <div class="swiper-scrollbar"></div> -->
                </div>
                <!--swiper-container end-->
            </div>
            <!--banner end-->
            <nav>
                <div class="yo-ico">&#xe621;</div>
                <div class="yo-ico">&#xe603;</div>
                <div class="yo-ico">&#xe625;</div>
                <div class="yo-ico">&#xe6a5;</div>
            </nav>
            <section>
                <!--<article>
                    <summary>                       
                    </summary>                        
                    <figure>
                        <figcaption></figcaption>
                        <img src="">                            
                    </figure> 
                </article>-->

                <article id="iscrollLoading">
                    <div class="is_title">
                        小乐一下
                    </div>
                    <!--<div id="iscrollWrapper">
                        <ul>

                        </ul>
                    </div>-->
                    <home-iscroll></home-iscroll>
                </article>
            </section>
            <!-- <aside>
            </aside>
            -->
        </div>
        <!--mainWrapper end-->
    </div>
</template>

<script>
  import common from '@/utils/util.common.js'
  import HomeIscroll from './homeIscroll.vue'
  import axios from 'axios'
  export default {
    name: 'home',
    components:{
      HomeIscroll
    },
    data () {
      return {
        swip : []
      }
    },
    mounted () {
      var that = this
      // common.ajax("POST",'/api/indexswip',null,function (res) {
      //   that.swip = res
      // })
      axios.post('/api/indexswip')
      .then(function (response) {
        // console.log(response)
        that.swip = response.data
      })
      .catch(function (error) {
        console.log(error)
      })
    },
    updated () {
      let banswip = common.packageSwiper(".swiper_c_modi");
    }
  }

</script>


