<template>
<div id="m-b-template">
  <div class="m-b-tem-main">
    <ul>
      <li><h2>排序算法</h2></li>
      <li>
        <h3>冒泡排序</h3>
        <p>
          <textarea :style="s" :wrap="off">
            如下：
            for(var i=0; i < arr.length; i++){
                for(var j=0; j < arr.length-i-1; j++){
                    if(arr[j] < arr[j+1]){
                        var temp = arr[j];
                        arr[j] = arr[j+1];
                        arr[j+1] = temp;
                    }
                }
            }

          </textarea>
        </p>      
      </li>
      <li>
        <h3>选择排序</h3>
        <p>
          <textarea :style="s" :wrap="off">
            如下：
            var arr = [1,3,5,6,4];
            for(var i=0; i < arr.length; i++){
              for(var j=i; j < arr.length; j++){
                if(arr[i] < arr[j]){
                    var temp = arr[i];
                    arr[i] = arr[j];
                    arr[j] = temp;
                }
              }
            }
          </textarea>
        </p>      
      </li>
      <li>
        <h3>快速排序</h3>
        <p>
          <textarea :style="s" :wrap="off">
            function quicksort(arr){
                if(arr.length <= 1) return arr;  //结束条件
                var midIndex = parseInt(arr.length/2); 
                var mid = arr[midIndex];

                var left = [];
                var right = [];
                for(var i=0; i < arr.length; i++){
                    if(i == midIndex) continue;
                    if(arr[i]<=mid) {
                        left.push(arr[i]);
                    } else {
                        right.push(arr[i]);
                    }
                }
                // 递归调用  F(arr) = F(left) + mid + F(right)
                return quicksort(left).concat(mid).concat(quicksort(right));
            }
          </textarea>
        </p>      
      </li>
    </ul>
  </div>
  <div class="m-b-tem-comment">
    <Comment v-bind:dataId="dataId" v-if="dataId" v-bind:commeRes="commeRes"></Comment>
  </div>

</div>
</template>


<script>
  import Comment from './comment'
  export default {
    name:'blog-template',
    props:['essayData','dataId','commeRes'],
    components: {
      Comment
    },
    data () {
      return {
        commeRes1:this.commeRes,
        s:{
					'resize': 'none',
					'overflow-x': 'scroll !important',
					'width': '100%',
					'height': '100%',
					'white-space':'pre',
          'border':'#000 solid 1px'
				}
      }
      
    },
    computed: {
      commeRes0: function () {
        return this.commeRes
      }
    },
    mounted () {

    },
    updated () {
      // console.log(this.commeRes1+"commeRes1")// data中不行
      // console.log(this.commeRes0+"commeRes0")//computed 也可以
      // console.log(this.commeRes+"commeRestemplate")//直接引用prop（是个数组，包含若干对象）可以
    }
  }
</script>

<style scoped>
  .m-b-tem-main{
    width:100%;
  }
  .m-b-tem-main ul{
    padding: .1rem;
    padding-bottom: .7rem;
  }
  .m-b-tem-main ul li{
    margin: 0.05rem 0;
  }
  .m-b-tem-main ul li h2{
    font-size:.30rem;
    color:#000;
    font-weight: bold;
  }
  .m-b-tem-main ul li h3{
    font-size:.20rem;
    color:#000;
  }
  .m-b-tem-main ul li img{
    width:100%;
    height: 100%;
  }
  .m-b-tem-main ul li p{
    font-size: .16rem;
    color:#1E1E1E;
    margin: 0.1rem 0;
    height: 2rem;
  }

  @media all and (min-width:1025px){
      .m-b-tem-main ul{
        padding-bottom: .1rem;
      }
      .m-b-tem-main ul li{
        margin: 0.05rem 0;
      }
      .m-b-tem-main ul li h2{
        font-size:0.1rem;
        color:#000;
        font-weight: bold;
      }
      .m-b-tem-main ul li h3{
        font-size:0.06rem;
        color:#000;
      }
      .m-b-tem-main ul li img{
        width:50%;
        display: block;
      }
      .m-b-tem-main ul li p{
        font-size: 0.04rem;
        color:#1E1E1E;
        margin: 0.05rem 0;
        height: 0.6rem
      }
  }
</style>


