<template>
  <div id="m-comment">
    <div class="comment_title">留言板</div>
    <div class="comment_content">
      <ul>
        <li v-if="sta" v-for="item in commeRes">
          <figcaption class="c_c_lou">{{ item.uid }}楼</figcaption>
          <span class="c_c_pinglun">
            <figcaption>用户{{ item.usName }}</figcaption>
            <p v-html="item.content"></p>
          </span>
        </li>
        <li v-if="!sta" v-for="item in ctt">
          <figcaption class="c_c_lou">{{ item.uid }}楼</figcaption>
          <span class="c_c_pinglun">
            <figcaption>用户{{ item.usName }}</figcaption>
            <p v-html="item.content"></p>
          </span>
        </li>
      </ul>
    </div>
    <form id="comm_from" @submit.prevent>
        <textarea name="content" id="content"></textarea>
        <br/>
        <input type="submit" name="" value="提交" v-on:click="tearea()">
    </form>
  </div>
</template>

<script>
  import common from '@/utils/util.common.js' 
  export default {
    props:['dataId','commeRes'],
    data: function () {
      return {
        sta: true,
        ctt: []
      }
    },
    computed: {
      dataId2 () {
        return this.dataId
      },
      commeRes2 () {
        return this.commeRes
      },
      usn () {
        return this.$store.getters.getUserName
      }
    },
    methods: {
      tearea () {
        // 获取表单数据
        // var formObj = document.getElementById("comm_from")
        // var formStr = common.formToString(formObj)
        // console.log(formStr+"formStr")
        //editMode中获取内容
        var editMode = document.getElementById("xhe0_iframe").contentWindow.document.body
        // console.log(editMode)
        var formStr = '&content='+editMode.innerHTML
        // console.log(formStr)
        // 提交
        var that = this
        var connName = 'connName='+that.dataId2+'comme'
        // console.log(connName+"connName")
        // 用户名
        var usnStr
        if (that.usn) {
          usnStr = '&usn='+that.usn
        } else {
          usnStr = '&usn='+Math.round(Math.random()*1000)
        }
        //总字符串
        var strNum = connName+formStr+usnStr
        // console.log(strNum+"strNum")
        common.ajax("POST",'/api/submit',strNum,function (res) {
          // console.log("_____________________________________")
          // console.log(res)
          that.ctt = res
          // console.log(that.ctt[0])
          // console.log("________________________________________")
          that.sta = false
        })
      
      }
    },
    mounted () {
      pageInit()
        function pageInit() {
            $.extend(XHEDITOR.settings, {shortcuts:{'ctrl+enter': submitForm}})
            $('#content').xheditor({
                html5Upload: false,
                upMultiple: '1',

                upLinkUrl: 'upload.html',
                upLinkExt: 'zip, rar, txt',

                upImgUrl: './comment/uploadImg',
                upImgExt: 'jpg,jpeg,gif,png',

                upFlashUrl: 'upload.html',
                upFlashExt: 'swf',

                upMediaUrl: 'upload.html',
                upMediaExt: 'wmv,avi,wma,mp3,mid'
            })
        }
        function insertUpload(arrMsg) {
            var i, msg
            for (i = 0; i < arrMsg.length; i++) {
                msg = arrMsg[i]
                $("#uploadList").append('<option value="'+msg.id+'">'+msg.localname+'</option>')
            }
        }
        function submitForm() {
            $('#frmDemo').submit()
        }
    },
    beforeUpdate () {
      // console.log(this.commeRes)
      // console.log(this.commeRes2[0].uid+"commeRes2")
    }
  }
</script>

<style scoped>
  #m-comment{
    width:100%;
    margin-bottom: 1rem;
  }
  #m-comment .comment_title {
    font-size: .2rem;
    margin-left: .1rem;
    margin-bottom: .1rem;
    border-bottom: #000 solid 0.02rem;
  }
  #m-comment form{
    font-size:.20rem;
  }
  #m-comment form input{
    width: .5rem;
    height: .3rem;
    border-radius: .05rem;
    float: right;
    margin-right: .2rem;
   
  }
  #m-comment form textarea{
    width: 100%;
    height: 2rem;
  }
  #m-comment .comment_content ul li{
    border: #000 dashed 2px;
    padding: .1rem;
    display: flex;
    justify-content: flex-start;
  }
  #m-comment .comment_content ul li .c_c_lou{
    width:10%;
    font-size: .2rem;
    padding-right: 0.05rem;
    border-right: #000 dashed 1px;
  }
  #m-comment .comment_content ul li .c_c_pinglun{
    width: 90%;
    font-size: .18rem;
    display: flex;
    flex-direction: column;
  }
  #m-comment .comment_content ul li .c_c_pinglun p{
    width: 100%; 
    padding:0.05rem;
    word-wrap: break-word;
  }
  #m-comment .comment_content ul li .c_c_pinglun figcaption{
    border-bottom: #000 dashed 1px;
    width: 100%;
    padding:0.05rem;
  }
  #comm_from{
    margin-left: .1rem;
    margin-right: .1rem;
  }
  #m-comment .comment_content{
    margin-left: .1rem;
    margin-right: .1rem;
  }
  @media all and (min-width:1025px){
    #m-comment form{
      font-size:.10rem;
    }
    #m-comment form input{
      width: .3rem;
      height: .15rem;
      border-radius: .05rem;
      float: right;
      margin-right: .2rem;  
    }
    #m-comment form textarea{
      width: 100%;
      height: 0.4rem;
    }
    #m-comment .comment_content ul li{
      border: #000 dashed 2px;
      padding: .025rem;
      display: flex;
      justify-content: flex-start;
    }
    #m-comment .comment_content ul li .c_c_lou{
      width:10%;
      font-size: .08rem;
      padding-right: 0.025rem;
      border-right: #000 dashed 1px;
    }
    #m-comment .comment_content ul li .c_c_pinglun{
      width: 90%;
      font-size: .06rem;
      display: flex;
      flex-direction: column;
    }
    #m-comment .comment_content ul li .c_c_pinglun p{
      width: 100%; 
      padding:0.025rem;
      word-wrap: break-word;
    }
    #m-comment .comment_content ul li .c_c_pinglun figcaption{
      border-bottom: #000 dashed 1px;
      width: 100%;
      padding:0.025rem;
    }
  }
</style>


