<template>
	<div id="m-login">
		<form action="" method="POST" @submit.prevent>
			用户名:<input type="text" name="username" value="" required maxlength="11" placeholder="请输入用户名" v-model="uname"></br></br>
			密&nbsp;码:<input type="password" name="password" value="" required maxlength="11" v-model="mima"></br></br>
			<input type="submit" name="submit" value="登录" v-on:click="sublog()">
		</form>
	</div>
</template>

<script>
import common from '@/utils/util.common.js' 
export default {
	name: 'login',
	data: function() {
		return {
			uname:'',
			mima:''
		}
	},
	methods:{
		sublog: function () {
			var that = this;
			// console.log(that.uname)
			// console.log(that.mima)
			common.ajax("POST",'/api/login','username='+that.uname+'&password='+that.mima,function (res) {
				// console.log(res)
				that.$store.dispatch('commitUserName',res)
				// 设置cookie存储数据
				var date = new Date()
				// console.log(date.getMinutes())
				date.setMinutes(date.getMinutes()+1)
				// console.log(date.setMinutes(date.getMinutes()+1))
				document.cookie = "usernam="+res+";expires="+date
				that.$router.push('/')
			})
			
		}
		
	},
	beforeCreate () {
		// console.log('beforeCreate')
	},
	created () {
		// console.log('created')		
	},
	beforeMount () {
		// console.log('beforeMount')
	},
	mounted () {
		// console.log('mounted')
	},
	beforeUpdate () {
		// console.log('beforeUpdate')
	},
	updated () {
		// console.log('updated')
	},
	activated () {
		// console.log('activated')
	},
	deactivated () {
		// console.log('deactivated')
	},
	beforeDestroy () {
		// console.log('beforeDestroy')
	},
	destroyed () {
		// console.log('destroyed')
	}
}
</script>

<style>
	#m-login{
		margin: .2rem;
	}
	#m-login form {
		width:100%;
		font-size:.2rem;
	}
	#-login form input {
		width:100%;
	}
</style>


