<template>
<div id="m-b-template">
  <div class="m-b-tem-main">
    <ul>
      <li><h2>接口文件</h2></li>
      <li>
        <h3>接口</h3>
        <p>
          <textarea :style="s" :wrap="off">
           <?php
				@mysql_connect("localhost:3306","root","123456")or die("mySql出错");
				mysql_select_db("conn")or die("数据库连接失败");
				mysql_query("set names 'utf8'");
				header("Content-type:text/html;charset='utf-8'");
				header("Access-Control-Allow-Origin:*");
				$page = 0;
				$pageNum = 10;
				if( @!is_null($_GET["page"]) ){
					$page = $_GET["page"];
				};
				if( @!is_null($_GET["pageNum"]) ){
					$pageNum = $_GET["pageNum"];
				};
				$sql = "SELECT * FROM `lxury` LIMIT $page,$pageNum";
				$query = mysql_query($sql);
				$row = mysql_fetch_assoc($query);
				$list = array();
				$i = 0;
				while( $row = mysql_fetch_assoc($query) ){
					$list[$i] = $row;
					$i++;
				};
				echo json_encode(array("dataList"=>$list));
				mysql_free_result($query); //请求结束后释放数据库
				mysql_close(); //关闭数据库
			?>
          </textarea>
        </p>      
      </li>
    </ul>
  </div>
  <div class="m-b-tem-comment">
    <Comment v-bind:dataId="dataId" v-if="dataId" v-bind:commeRes="commeRes"></Comment>
  </div>

</div>
</template>


<script>
  import Comment from './comment'
  export default {
    props:['essayData','dataId','commeRes'],
    components: {
      Comment
    },
    data () {
      return {
        commeRes1: this.commeRes,
        s:{
          'resize': 'none',
          'overflow-x': 'scroll !important',
          'width': '100%',
          'height': '100%',
          'white-space':'pre',
          'border':'#000 solid 1px'
	    }
      }
      
    },
    computed: {
      commeRes0: function () {
        return this.commeRes
      }
    },
    mounted () {

    },
    updated () {
      // console.log(this.commeRes1+"commeRes1")// data中不行
      // console.log(this.commeRes0+"commeRes0")//computed 也可以
      // console.log(this.commeRes+"commeRestemplate")//直接引用prop（是个数组，包含若干对象）可以
    }
  }
</script>

<style scoped>
  .m-b-tem-main{
    width:100%;
  }
  .m-b-tem-main ul{
    padding: .1rem;
    padding-bottom: .7rem;
  }
  .m-b-tem-main ul li{
    margin: 0.05rem 0;
  }
  .m-b-tem-main ul li h2{
    font-size:.30rem;
    color:#000;
    font-weight: bold;
  }
  .m-b-tem-main ul li h3{
    font-size:.20rem;
    color:#000;
  }
  .m-b-tem-main ul li img{
    width:100%;
    height: 100%;
  }
  .m-b-tem-main ul li p{
    font-size: .16rem;
    color:#1E1E1E;
    margin: 0.1rem 0;
    height: 2rem;
  }

  @media all and (min-width:1025px){
      .m-b-tem-main ul{
        padding-bottom: .1rem;
      }
      .m-b-tem-main ul li{
        margin: 0.05rem 0;
      }
      .m-b-tem-main ul li h2{
        font-size:0.1rem;
        color:#000;
        font-weight: bold;
      }
      .m-b-tem-main ul li h3{
        font-size:0.06rem;
        color:#000;
      }
      .m-b-tem-main ul li img{
        width:50%;
        display: block;
      }
      .m-b-tem-main ul li p{
        font-size: 0.04rem;
        color:#1E1E1E;
        margin: 0.05rem 0;
        height: 0.6rem
      }
  }
</style>


