<template>
     <div id="iscrollWrapper">
        <div class="scroll-contain">
            <div class="head" v-bind:class="{ hide:isTrue }">
                <img :src="src" width="40" height="40" v-bind:class="{ up:headFalse }"/>
                <span>下拉刷新...</span>
            </div>
            <ul id="shoplist">
                <li v-for="item in isc">
                    <img :src="item.src"/>
                    <h3>{{ item.title }}</h3>
                    <p>{{ item.info }}</p>
                </li>
            </ul>
            <div class="foot" v-bind:class="{ hide:isTrue }">
                <img :src="src" width="40" height="40" v-bind:class="{ down:footFalse }"/>
                <span>上拉加载更多...</span>
            </div>
        </div>
    </div>
</template>

<script>
    import common from '@/utils/util.common.js'
    import axios from 'axios'
    export default {
        name : 'iscroll',
        data () {
            return {
              isc : [],
              isTrue:true,
              headFalse:false,
              footFalse:false,
              src:'/static/images/arrow.png'
            }
        },
        mounted () {
            var that = this
            // common.ajax("POST",'/api/iscroll',"len=2&n=0",function (res) {
            //   //console.log(res)
            //   that.isc = res
            //   setTimeout(function () {
            //       common.isAllLoaded('shoplist',function(){
            //           common.scroll(that);
            //       })

            //   }, 1000)
            // })
            axios.post('/api/iscroll',{
              len: 2,
              n: 0
            })
            .then(function (response) {
              // console.log(response)
              that.isc = response.data
              setTimeout(function () {
                  common.isAllLoaded('shoplist',function(){
                      common.scroll(that);
                  })

              }, 1000)
            })
            .catch(function (error) {
              console.log(error)
            })
        }
    }
</script>

<style>
    .head {
        text-align: center;
        height: .35rem;
        line-height: .35rem;
    }
    .head img {
        width: .14rem;
        height: .14rem;
        display: inline-block;
        transform: rotate(180deg);
        -webkit-transform: rotate(180deg);
        transition: transform 150ms;
        -webkit-transition: transform 150ms;
    }
    .head.hide{
        display: none !important;
    }
    .up {
        transform: rotate(360deg) !important;
        -webkit-transform: rotate(360deg) !important;
    }
    .foot {
        text-align: center;
        height: .35rem;
        line-height: .35rem;
    }
    .foot img {
        width: .14rem;
        height: .14rem;
        display: inline-block;
        /*transform: rotate(0deg);*/
        /*-webkit-transform: rotate(0deg);*/
        transition: transform 500ms;
        -webkit-transition: transform 500ms;
    }
    .foot.hide{
        display: none !important;
    }
    .down {
        transform: rotate(-180deg) !important;
        -webkit-transform: rotate(-180deg) !important;
    }
    .head span,.foot span {
        font-size: .18rem;
    }
    @media all and (min-width:1025px){
        .head {
            display: none !important;
        }
         .foot {
            display: none !important;
        }
    } 
</style>


