<template>
<div id="m-b-template">
  <div class="m-b-tem-main">
    <ul>
      <li><h2>fs文件系统</h2></li>
      <li>
        <h3>查看状态</h3>
        <p>
          <textarea :style="s" :wrap="off">
            const fs = require('fs')

            fs.stat('hello.js',(err,stats) => {
                if(err){
                    console.log(err)
                }else{
                    //查看状态
                    console.log(stats)
                    //是否是文件
                    console.log(`文件：${stats.isFile()}`)
                    //是否是目录
                    console.log(`目录 :${stats.isDirectory()}`)
                    
                }
            })
          </textarea>
        </p>      
      </li>
      <li>
        <h3>创建目录</h3>
        <p>
          <textarea :style="s" :wrap="off">
            const fs = require('fs')

            fs.mkdir('logs',(error) => {
                console.log('成功创建目录')
            })
          </textarea>
        </p>      
      </li>
      <li>
        <h3>追加文件内容</h3>
        <p>
          <textarea :style="s" :wrap="off">
            const fs = require('fs')

            fs.writeFile('logs/hello.log','你好\n',(error) =>{
                if(error) {
                    console.log(error)
                }else{
                    console.log('文件创建成功')
                }
            })
            //追加文件内容
            fs.appendFile('logs/hello.log','hello\n',(error) =>{
                if(error) {
                    console.log(error)
                }else{
                    console.log('文件成功写入')
                }
            })
          </textarea>
        </p>      
      </li>
      <li>
        <h3>异步的读取一个文件的全部内容</h3>
        <p>
          <textarea :style="s" :wrap="off">
            const fs = require('fs')

            fs.readFile('logs/hello.log','utf8',(err,data) => {
                if(err){
                    console.log(err)
                }else{
                    console.log(data)
                }
            })
          </textarea>
        </p>      
      </li>
      <li>
        <h3>读取目录</h3>
        <p>
          <textarea :style="s" :wrap="off">
            const fs = require('fs')
            //读取目录
            fs.readdir('logs',(err,files) => {
                console.log(files)
                console.log(files[0])
            })
          </textarea>
        </p>      
      </li>
      <li>
        <h3>重命名</h3>
        <p>
          <textarea :style="s" :wrap="off">
            const fs= require('fs')
            //重命名
            fs.rename('logs/hello.log','logs/greeting.log',(err)=>{
                console.log('重命名成功')
            })
          </textarea>
        </p>      
      </li>
      <li>
        <h3>删除</h3>
        <p>
          <textarea :style="s" :wrap="off">
            const fs = require('fs')
            //要删除要先读取目录
            fs.readdirSync('logs').map((file)=>{
                //删除文件
                fs.unlink(`logs/${file}`,(error)=>{
                    if(error){
                        console.log('error')
                    }else{
                        console.log('success')
                    }
                })
            })
            //删除目录
            fs.rmdir('logs',(err)=>{
                if(err){
                    console.log('err')
                }else{
                    console.log('deleted')
                }
            })
          </textarea>
        </p>      
      </li>
    </ul>
  </div>
  <div class="m-b-tem-comment">
    <Comment v-bind:dataId="dataId" v-if="dataId" v-bind:commeRes="commeRes"></Comment>
  </div>

</div>
</template>


<script>
  import Comment from './comment'
  export default {
    props:['essayData','dataId','commeRes'],
    components: {
      Comment
    },
    data () {
      return {
        commeRes1: this.commeRes,
        s:{
          'resize': 'none',
          'overflow-x': 'scroll !important',
          'width': '100%',
          'height': '100%',
          'white-space':'pre',
          'border':'#000 solid 1px'
	      }
      }
      
    },
    computed: {
      commeRes0: function () {
        return this.commeRes
      }
    },
    mounted () {

    },
    updated () {
      // console.log(this.commeRes1+"commeRes1")// data中不行
      // console.log(this.commeRes0+"commeRes0")//computed 也可以
      // console.log(this.commeRes+"commeRestemplate")//直接引用prop（是个数组，包含若干对象）可以
    }
  }
</script>

<style scoped>
  .m-b-tem-main{
    width:100%;
  }
  .m-b-tem-main ul{
    padding: .1rem;
    padding-bottom: .7rem;
  }
  .m-b-tem-main ul li{
    margin: 0.05rem 0;
  }
  .m-b-tem-main ul li h2{
    font-size:.30rem;
    color:#000;
    font-weight: bold;
  }
  .m-b-tem-main ul li h3{
    font-size:.20rem;
    color:#000;
  }
  .m-b-tem-main ul li img{
    width:100%;
    height: 100%;
  }
  .m-b-tem-main ul li p{
    font-size: .16rem;
    color:#1E1E1E;
    margin: 0.1rem 0;
    height: 2rem;
  }

  @media all and (min-width:1025px){
      .m-b-tem-main ul{
        padding-bottom: .1rem;
      }
      .m-b-tem-main ul li{
        margin: 0.05rem 0;
      }
      .m-b-tem-main ul li h2{
        font-size:0.1rem;
        color:#000;
        font-weight: bold;
      }
      .m-b-tem-main ul li h3{
        font-size:0.06rem;
        color:#000;
      }
      .m-b-tem-main ul li img{
        width:50%;
        display: block;
      }
      .m-b-tem-main ul li p{
        font-size: 0.04rem;
        color:#1E1E1E;
        margin: 0.05rem 0;
        height: 0.6rem
      }
  }
</style>


