<template>
<div id="m-b-template">
  <div class="m-b-tem-main">
    <ul>
      <li><h2>常见兼容问题总结</h2></li>
      <li>
        <h3>获取Event对象</h3>
        <p>
          <textarea :style="s" :wrap="off">
            IE:   window.event
            非IE：  function(evt){

            }
            兼容写法 : function(evt){
                var e = evt || event;
            }
          </textarea>
        </p>      
      </li>
      <li>
        <h3>获取键盘码</h3>
        <p>
          <textarea :style="s" :wrap="off">
            e.keyCode || e.which;
          </textarea>
        </p>      
      </li>
      <li>
        <h3>获取事件触发源对象</h3>
        <p>
          <textarea :style="s" :wrap="off">
            获取事件触发源对象
          </textarea>
        </p>      
      </li>
      <li>
        <h3>获取行内样式</h3>
        <p>
          <textarea :style="s" :wrap="off">
            var getStyle = (function(){
                if(window.VBArray){
                    return function(ele, attr){
                        return ele.currentStyle[attr];  
                    } 
                }else{
                    return function(ele, attr){
                        return getComputedStyle(ele,null)[attr]; 
                    } 
                }
            })();
          </textarea>
        </p>      
      </li>
      <li>
        <h3>添加事件监听</h3>
        <p>
          <textarea :style="s" :wrap="off">
            var addEvent = (function(){
                if(window.VBArray){
                      return function(ele, eventname, func){
                          ele.attachEvent("on"+eventname, func);
                      }
                } else {
                      return function(ele, eventname, func, isCapture){
                          ele.addEventListener(eventname, func, isCapture);
                      }
                }
            })();
          </textarea>
        </p>      
      </li>
      <li>
        <h3>获取页面的滚动距离</h3>
        <p>
          <textarea :style="s" :wrap="off">
            var scrolltop = document.documentElement.scrollTop || document.body.scrollTop
          </textarea>
        </p>      
      </li>
      <li>
        <h3>阻止冒泡</h3>
        <p>
          <textarea :style="s" :wrap="off">
            e.stopPropagation ? e.stopPropagation()  : e.cancelBubble = true;
          </textarea>
        </p>      
      </li>
      <li>
        <h3>阻止浏览器的默认行为</h3>
        <p>
          <textarea :style="s" :wrap="off">
            e.preventDefault ? e.preventDefault() :  e.returnValue = false;
          </textarea>
        </p>      
      </li>
    </ul>
  </div>
  <div class="m-b-tem-comment">
    <Comment v-bind:dataId="dataId" v-if="dataId" v-bind:commeRes="commeRes"></Comment>
  </div>

</div>
</template>


<script>
  import Comment from './comment'
  export default {
    name:'blog-template',
    props:['essayData','dataId','commeRes'],
    components: {
      Comment
    },
    data () {
      return {
        commeRes1:this.commeRes,
        s:{
					'resize': 'none',
					'overflow-x': 'scroll !important',
					'width': '100%',
					'height': '100%',
					'white-space':'pre',
          'border':'#000 solid 1px'
				}
      }
      
    },
    computed: {
      commeRes0: function () {
        return this.commeRes
      }
    },
    mounted () {

    },
    updated () {
      // console.log(this.commeRes1+"commeRes1")// data中不行
      // console.log(this.commeRes0+"commeRes0")//computed 也可以
      // console.log(this.commeRes+"commeRestemplate")//直接引用prop（是个数组，包含若干对象）可以
    }
  }
</script>

<style scoped>
  .m-b-tem-main{
    width:100%;
  }
  .m-b-tem-main ul{
    padding: .1rem;
    padding-bottom: .7rem;
  }
  .m-b-tem-main ul li{
    margin: 0.05rem 0;
  }
  .m-b-tem-main ul li h2{
    font-size:.30rem;
    color:#000;
    font-weight: bold;
  }
  .m-b-tem-main ul li h3{
    font-size:.20rem;
    color:#000;
  }
  .m-b-tem-main ul li img{
    width:100%;
    height: 100%;
  }
  .m-b-tem-main ul li p{
    font-size: .16rem;
    color:#1E1E1E;
    margin: 0.1rem 0;
    height: 2rem;
  }

  @media all and (min-width:1025px){
      .m-b-tem-main ul{
        padding-bottom: .1rem;
      }
      .m-b-tem-main ul li{
        margin: 0.05rem 0;
      }
      .m-b-tem-main ul li h2{
        font-size:0.1rem;
        color:#000;
        font-weight: bold;
      }
      .m-b-tem-main ul li h3{
        font-size:0.06rem;
        color:#000;
      }
      .m-b-tem-main ul li img{
        width:50%;
        display: block;
      }
      .m-b-tem-main ul li p{
        font-size: 0.04rem;
        color:#1E1E1E;
        margin: 0.05rem 0;
        height: 0.6rem
      }
  }
</style>


