<template>
<div id="m-b-template">
  <div class="m-b-tem-main">
    <ul>
      <li><h2>vue指令</h2></li>
      <li>
        <h3>v-text</h3>
        <p>
          <textarea :style="s" :wrap="off">
            //原样输出
            &lt;span v-text="msg">&lt;/span>
            <!-- 和下面的一样 -->
            &lt;span>{{msg}}&lt;/span>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-html</h3>
        <p>
          <textarea :style="s" :wrap="off">
            //解析HTML标签
            &lt;div v-html="html">&lt;/div>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-show</h3>
        <p>
          <textarea :style="s" :wrap="off">
            根据表达式之真假值，切换元素的 display CSS 属性。已经渲染出来了
            &lt;h1 v-show="ok">Hello! &lt;/h1>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-if</h3>
        <p>
          <textarea :style="s" :wrap="off">
            同v-show的一个区别，v-if要重新渲染，当为false时移除
            &lt;h1 v-if="ok">Yes &lt;/h1>
            &lt;h1 v-else>No &lt;/h1>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-else-if</h3>
        <p>
          <textarea :style="s" :wrap="off">
            与 v-else 相似,，v-else-if 必须跟在 v-if 或者 v-else-if之后
            &lt;div v-if="type === 'A'">
              A
            &lt;/div>
            &lt;div v-else-if="type === 'B'">
              B
            &lt;/div>
            &lt;div v-else-if="type === 'C'">
              C
            &lt;/div>
            &lt;div v-else>
              Not A/B/C
            &lt;/div>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-for</h3>
        <p>
          <textarea :style="s" :wrap="off">
            &lt;div v-for="item in items">
              &lt;ul>
                &lt;li>{{ "item.属性" }}&lt;/li>
              &lt;/ul>
            &lt;/div>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-on</h3>
        <p>
          <textarea :style="s" :wrap="off">
            绑定事件监听器,只能监听 原生 DOM 事件
            <!-- 内联语句 -->
            &lt;button v-on:click="doThat('hello', $event)">&lt;/button>
            <!-- 缩写 -->
            &lt;button @click="doThis">&lt;/button>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-bind</h3>
        <p>
          <textarea :style="s" :wrap="off">
            绑定属性
            <!-- 绑定一个属性 -->
            &lt;img v-bind:src="imageSrc">
            <!-- 缩写 -->
            &lt;img :src="imageSrc">
            <!-- class 绑定 -->
            &lt;div :class="{ red: isRed }">&lt;/div>
            &lt;div :class="[classA, classB]">&lt;/div>
            &lt;div :class="[classA, { classB: isB, classC: isC }]">
            <!-- style 绑定 -->
            &lt;div :style="{ fontSize: size + 'px' }">&lt;/div>
            &lt;div :style="[styleObjectA, styleObjectB]">&lt;/div>
            <!-- prop 绑定. “prop” 必须在 my-component 中声明。 -->
            &lt;my-component :prop="someThing">&lt;/my-component>
            在组件中通过prop 获取
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-model</h3>
        <p>
          <textarea :style="s" :wrap="off">
            表单双向绑定
            &lt;input v-model="message" placeholder="edit me">
            &lt;p>Message is: {{ message }}&lt;/p>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-pre</h3>
        <p>
          <textarea :style="s" :wrap="off">
            跳过这个元素和它的子元素的编译过程
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-cloak</h3>
        <p>
          <textarea :style="s" :wrap="off">
            这个指令保持在元素上直到关联实例结束编译。和 CSS 规则如 [v-cloak] { display: none } 一起用时，这个指令可以隐藏未编译的 Mustache 标签直到实例准备完毕。
            [v-cloak] {
              display: none;
            }
            &lt;div v-cloak>
              {{ message }}
            &lt;/div>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-once</h3>
        <p>
          <textarea :style="s" :wrap="off">
            只渲染元素和组件一次。随后的重新渲染,元素/组件及其所有的子节点将被视为静态内容并跳过。这可以用于优化更新性能。
            <!-- 单个元素 -->
            &lt;span v-once>This will never change: {{msg}}&lt;/span>
            <!-- 有子元素 -->
            &lt;div v-once>
              &lt;h1>comment&lt;/h1>
              &lt;p>{{msg}}&lt;/p>
            &lt;/div>
            <!-- 组件 -->
            &lt;my-component v-once :comment="msg">&lt;/my-component>
            <!-- v-for 指令-->
            &lt;ul>
              &lt;li v-for="i in list" v-once>{{i}}&lt;/li>
            &lt;/ul>
          </textarea>
        </p>      
      </li>
    </ul>
  </div>
  <div class="m-b-tem-comment">
    <Comment v-bind:dataId="dataId" v-if="dataId" v-bind:commeRes="commeRes"></Comment>
  </div>

</div>
</template>


<script>
  import Comment from './comment'
  export default {
    props:['essayData','dataId','commeRes'],
    components: {
      Comment
    },
    data () {
      return {
        commeRes1: this.commeRes,
        s:{
          'resize': 'none',
          'overflow-x': 'scroll !important',
          'width': '100%',
          'height': '100%',
          'white-space':'pre',
          'border':'#000 solid 1px'
				}
      }
      
    },
    computed: {
      commeRes0: function () {
        return this.commeRes
      }
    },
    mounted () {

    },
    updated () {
      // console.log(this.commeRes1+"commeRes1")// data中不行
      // console.log(this.commeRes0+"commeRes0")//computed 也可以
      // console.log(this.commeRes+"commeRestemplate")//直接引用prop（是个数组，包含若干对象）可以
    }
  }
</script>

<style scoped>
  .m-b-tem-main{
    width:100%;
  }
  .m-b-tem-main ul{
    padding: .1rem;
    padding-bottom: .7rem;
  }
  .m-b-tem-main ul li{
    margin: 0.05rem 0;
  }
  .m-b-tem-main ul li h2{
    font-size:.30rem;
    color:#000;
    font-weight: bold;
  }
  .m-b-tem-main ul li h3{
    font-size:.20rem;
    color:#000;
  }
  .m-b-tem-main ul li img{
    width:100%;
    height: 100%;
  }
  .m-b-tem-main ul li p{
    font-size: .16rem;
    color:#1E1E1E;
    margin: 0.1rem 0;
    height: 2rem;
  }

  @media all and (min-width:1025px){
      .m-b-tem-main ul{
        padding-bottom: .1rem;
      }
      .m-b-tem-main ul li{
        margin: 0.05rem 0;
      }
      .m-b-tem-main ul li h2{
        font-size:0.1rem;
        color:#000;
        font-weight: bold;
      }
      .m-b-tem-main ul li h3{
        font-size:0.06rem;
        color:#000;
      }
      .m-b-tem-main ul li img{
        width:50%;
        display: block;
      }
      .m-b-tem-main ul li p{
        font-size: 0.04rem;
        color:#1E1E1E;
        margin: 0.05rem 0;
        height: 0.6rem
      }
  }
</style>


